footer{
  background-color: var(--bg-color-variant);
  padding: 3rem 0;
  text-align: center;
  font-size: 0.9rem;
  margin-top: 7rem;
}

footer a{
  color: var(--bg-color);
}

.footer__logo{
  font-size: 2rem;
  font-weight: 500;
  margin-bottom: 2rem;
  display: inline-block;
}

.permalinks {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 2rem;
  margin: 0 auto 3rem;
}

.footer__socials{
  display: flex;
  justify-content: center;
  gap: 1rem;
  margin-bottom: 4rem;
}

.footer__socials a{
  background-color: var(--bg-color);
  color: var(--white-color);
  padding: 0.8rem;
  border-radius: 0.7rem;
  display: flex;
  border: 1px solid transparent;
}

.footer__socials a:hover{
  background: transparent;
  color: var(--bg-color);
  border-color: var(--bg-color);
}

.footer__copyright{
  margin-bottom: 4rem;
  color: var(--bg-color);
}

/* SMALL DEVICES MEDIA QUERIES */
@media screen and (max-width:600px){
  .permalinks{
    flex-direction: column;
    gap: 1.5rem;
  }

  .footer__socials{
    margin-bottom: 2.6rem;
  }
}