nav {
  background: rgba(0, 0, 0, 0.3);
  width: max-content;
  display: block;
  padding: 0.7rem;
  z-index: 2;
  position: fixed;
  left: 50%;
  transform: translateX(-50%);
  bottom: 2rem;
  display: flex;
  gap: 0.8rem;
  backdrop-filter: blur(15px);
  border-radius: 3rem;
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  display: flex;
  color: var(--light-color);
  font-size: 1.1rem;
}

nav a:hover {
  background: rgba(0, 0, 0, 0.4);
}

nav a.active{
  background: var(--color-primary-variant);
  color: var(--bg-color);
}